function checkNumDigits(n) {
  return n < 10 ? '0' + n : n;
}

const getTimerDisplay = (timerInSeconds) => ({
  minutes: checkNumDigits(Math.floor(timerInSeconds / 60)),
  seconds: checkNumDigits(timerInSeconds % 60),
});

const beep = function() {
  const audio = new Audio(
    'data:audio/wav;base64,'
  );
  audio.play();
};

export { getTimerDisplay, beep };
